import React from 'react'
import { Link } from 'react-static'

import logoFull from '../assets/img/logo-full.png'
import slogan from '../assets/img/slogan.png'
import iconFacebook from '../assets/img/icon-facebook.png'
import iconInstagram from '../assets/img/icon-instagram.png'
import iconEmail from '../assets/img/icon-email.png'

export default function(props) {
  const station = [
    { text: 'Flower Station', to: '/station' },
    { text: 'Recommend', to: '/station/recommend' },
    { text: 'Arrangement', to: '/station/arrangement' },
    { text: 'Bouquet', to: '/station/bouquet' },
    { text: 'Styling', to: '/station/styling' },
    { text: 'Event', to: '/station/event' }
  ]

  const studio = [
    { text: 'Flower Studio', to: '/studio' },
    { text: 'Introduction', to: '/studio/introduction' },
    { text: 'Flower design', to: '/studio/flower-design' },
    { text: 'DIY & NHKH', to: '/studio/diy' }
  ]

  return (
    <footer className="flex flex-wrap pa2 pv4-l bt b--navy f7">
      <div className="w-50 w-15-l flex flex-column items-center justify-center">
        <Link to="/" className="mt0 mb3">
          <img className="mw4" src={logoFull} alt="" />
        </Link>
        <p className="mt0 mb3">
          <img className="mw4" src={slogan} alt="" />
        </p>
      </div>

      <div className="w-50 w-20-l tc tl-l">
        <p className="lh-copy mv1">hello@eljulio.com</p>
        <a href="tel:0984802497" className="lh-copy mv1">
          0984 802 497
        </a>

        <ul className="list ma0 pa0 flex items-center justify-center justify-start-l mt4">
          <li className="mr2">
            <Link to="#">
              <img src={iconFacebook} alt="" />
            </Link>
          </li>
          <li className="mr2">
            <Link to="#">
              <img src={iconInstagram} alt="" />
            </Link>
          </li>
          <li className="mr2">
            <Link to="#">
              <img src={iconEmail} alt="" />
            </Link>
          </li>
        </ul>
      </div>

      <div className="w-100 w-50-l flex mv2 mv0-l">
        <ul className="list ma0 mr3 pl0 pr4-l">
          {station.map(link => (
            <li key={link.to}>
              <Link className="db mb2" to={link.to}>
                {link.text}
              </Link>
            </li>
          ))}
        </ul>

        <ul className="list ma0 mr3 pl0 pr4-l">
          {studio.map(link => (
            <li key={link.to}>
              <Link className="db mb2" to={link.to}>
                {link.text}
              </Link>
            </li>
          ))}
        </ul>

        <ul className="list ma0 mr3 pl0 pr4-l">
          <li>
            <Link className="db mb2" to="/about">
              About
            </Link>
          </li>
          <li>
            <Link className="db mb2" to="/how-to-order">
              How to Order
            </Link>
          </li>
          <li>
            <a className="db mb2" href="#" onClick={props.doOpenOrderForm}>
              Order Now
            </a>
          </li>
        </ul>
      </div>

      <div className="w-100 w-15-l tc tr-l mt3 mt0-l">
        {new Date().getFullYear()} &copy; Eljulio. All right reserved.
      </div>
    </footer>
  )
}



