import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData } from 'react-static'

import Layout from '../components/Layout'
import FlowerDetails from '../components/FlowerDetails'

function Header({ product }) {
  return (
    <div className="grid-col-span-9-l">
      <FlowerDetails product={product} />
    </div>
  )
}

export default withRouteData(function(props) {
  const { product } = props
  return (
    <div className="ph3-ns">
      <Helmet>
        <title>{product.name} :: ELJULIO</title>
      </Helmet>
      <Layout header={<Header product={product} />} />
    </div>
  )
})



