import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData } from 'react-static'

import {
  MENU_STATION,
  ARRANGEMENT_CATEGORIES,
  NUANCES,
  makeLabels
} from '../constants'

import PageHeader from '../components/PageHeader'
import Layout from '../components/Layout'
import FlowerGrid from '../components/FlowerGrid'
import FlowerFilter from '../components/FlowerFilter'

class Arrangement extends React.Component {
  state = {
    filter: {
      category: ARRANGEMENT_CATEGORIES.ALL,
      nuance: NUANCES.ALL
    }
  }

  doSetFilter = (type, value) => e => {
    this.setState({
      filter: {
        category: ARRANGEMENT_CATEGORIES.ALL,
        nuance: NUANCES.ALL,
        [type]: value
      }
    })
  }

  isFilterEnabled = (type, value) => this.state.filter[type] === value

  getVisible() {
    const { category, nuance } = this.state.filter
    const products = this.props.products || []

    if (category !== ARRANGEMENT_CATEGORIES.ALL)
      return products.filter(
        item => item.categories && item.categories.includes(category)
      )

    if (nuance !== NUANCES.ALL)
      return products.filter(
        item => item.nuances && item.nuances.includes(nuance)
      )

    return products
  }

  getHeader = () => {
    const categories = makeLabels(
      ARRANGEMENT_CATEGORIES,
      ARRANGEMENT_CATEGORIES.ALL
    )
    const nuances = makeLabels(NUANCES, NUANCES.ALL)

    return (
      <div className="grid-col-span-9-l f5 fw3">
        <PageHeader>Arrangement</PageHeader>
        <div className="ml6-l mt4-l">
          <p className="lh-copy dark-gray mt0 mb2 ph4">
            Bundling flowers is to bundle your mind. I create it in my hand, so
            I get a feeling straight. Together, the bouquet is tailored to you.
          </p>
          <p className="lh-copy dark-gray mt0 mb2 ph4">
            Custom-made floral bouquet. Handwork by expert florist surely bundle
            flowers with your sincerity. One of a kind bouquet for just only for
            you.
          </p>
        </div>

        <FlowerFilter
          categories={categories}
          nuances={nuances}
          doSetFilter={this.doSetFilter}
          isFilterEnabled={this.isFilterEnabled}
        />
      </div>
    )
  }

  getContent = () => {
    const items = this.getVisible()

    return items.length > 0 ? (
      <FlowerGrid items={items} />
    ) : (
      <div className="f-serif f4 i tc ma3">No item found</div>
    )
  }

  render() {
    return (
      <div className="ph3-ns">
        <Helmet>
          <title>Arrangement :: El Julio</title>
        </Helmet>
        <Layout header={this.getHeader()} submenu={MENU_STATION}>
          {this.getContent()}
        </Layout>
      </div>
    )
  }
}

export default withRouteData(Arrangement)



