import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData, Link } from 'react-static'

import Footer from '../components/Footer'
import PageHeader from '../components/PageHeader'

class Station extends React.Component {
  render() {
    const styles = Array.from({ length: 8 }, (_, i) => i + 1).map(i => ({
      backgroundImage: `url(https://res.cloudinary.com/duqeezi8j/image/upload/v1529417963/ej/flower-${i}.jpg)`
    }))

    return (
      <div className="ph3-ns">
        <Helmet>
          <title>Station :: El Julio</title>
        </Helmet>
        <div className="mv4-l pa2 pa0-l grid grid-1 grid-11-l grid-col-gap1 grid-row-gap1 grid-rows-4">
          <div
            className="grid-col-span-3-l cover bg-center"
            style={styles[7]}
          />
          <div className="grid-col-span-3-l self-center">
            <Link to="/station/recommend">
              <PageHeader withoutTilde={true}>Recommend</PageHeader>
            </Link>
          </div>
          <div className="grid-col-span-5-l cover bg-center" style={styles[6]}>
            <Link
              to="/station/styling"
              className="flex items-center justify-center h-100 flex-column relative">
              <div className="absolute absolute--fill bg-black-50 z-1" />
              <PageHeader withoutTilde={true} className="white z-2">
                Styling
              </PageHeader>
            </Link>
          </div>

          <div
            className="grid-col-span-5-l cover bg-center"
            style={styles[5]}
          />
          <div
            className="grid-col-span-3-l cover bg-center"
            style={styles[4]}
          />
          <div className="grid-col-span-3-l self-center">
            <Link to="/station/arrangement">
              <PageHeader withoutTilde={true}>Arrangement</PageHeader>
            </Link>
          </div>

          <div
            className="grid-col-span-3-l cover bg-center"
            style={styles[3]}
          />
          <div className="grid-col-span-3-l self-center">
            <Link to="/station/bouquet">
              <PageHeader withoutTilde={true}>Bouquet</PageHeader>
            </Link>
          </div>
          <div className="grid-col-span-5-l cover bg-center" style={styles[2]}>
            <Link
              to="/station/event"
              className="flex items-center justify-center h-100 flex-column relative">
              <div className="absolute absolute--fill bg-black-50 z-1" />
              <PageHeader withoutTilde={true} className="white z-2">
                Event
              </PageHeader>
            </Link>
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}

export default withRouteData(Station)



