import React from 'react'
import slogan from '../assets/img/slogan.png'
import flower from '../assets/img/flower-logo.png'

export default function(props) {
  return (
    <div className="flex flex-column justify-center align-center h-100 tc">
      <div className="mb4">
        <img src={flower} alt="" />
      </div>
      <p className="f3 fw7 f-serif ttu mv2">Thanks for choosing eljulio</p>
      <p className="f3 fw7 f-serif ttu mv2">Let us surprise you!</p>
      <div className="mt5">
        <img src={slogan} alt="" />
      </div>
    </div>
  )
}



