import React from 'react'
import classnames from 'classnames'
import tilde from '../assets/img/_tilde.png'

export default function(props) {
  return (
    <React.Fragment>
      <h1 className={classnames('f1 f-serif fw4 mt0 mb3 tc', props.className)}>
        {props.children}
      </h1>
      {props.withoutTilde ? null : (
        <p className="ma0 tc">
          <img src={tilde} alt="" className="mw3" />
        </p>
      )}
    </React.Fragment>
  )
}



