import React from 'react'
import ReactModal from 'react-modal'
import axios from 'axios'
import moment from 'moment'
import Animate from 'react-move/Animate'
import { withSiteData } from 'react-static'
import { easeQuadOut } from 'd3-ease'

import left from '../assets/img/art-left.png'
import right from '../assets/img/art-right.png'
import iconClose from '../assets/img/icon-close.png'

import PageHeader from './PageHeader'
import OrderSender from './OrderSender'
import OrderRecipient from './OrderRecipient'
import OrderPayment from './OrderPayment'
import OrderProduct from './OrderProduct'
import OrderThankYou from './OrderThankYou'

const FLOWS = [OrderProduct, OrderRecipient, OrderSender, OrderPayment]

class Cart extends React.Component {
  state = {
    submitting: false,
    submitted: false,
    error: null,
    currentViewIndex: 0,
    form: {
      product_type: 'Arrangement',
      purpose: 'Birthday',
      atmosphere: 'Recommendation',
      budget: 100000,
      delivery_date: moment()
        .startOf('hour')
        .add(4, 'hour'),
      greeting_card: 'yes_ej',
      card_content: '',
      card_type: 'handwriting',
      payment_type: 'cod',
      remarks: '',
      sender_name: '',
      sender_email: '',
      sender_company: '',
      sender_address: '',
      sender_ward: '',
      sender_district: '',
      sender_apartment_number: '',
      sender_phone: '',
      receiver_name: '',
      receiver_email: '',
      receiver_company: '',
      receiver_address: '',
      receiver_ward: '',
      receiver_district: '',
      receiver_apartment_number: '',
      receiver_phone: ''
    }
  }

  getCurrentView() {
    return FLOWS[this.state.currentViewIndex]
  }

  hasNext() {
    return this.state.currentViewIndex + 1 < FLOWS.length
  }

  hasPrev() {
    return this.state.currentViewIndex - 1 >= 0
  }

  isLast() {
    return this.state.currentViewIndex === FLOWS.length - 1
  }

  doSubmit = e => {
    e.preventDefault()
    if (this.state.submitting) return

    const req = axios.create({
      baseURL: this.props.Configurations.apiUrl
    })

    this.setState({ submitting: true })
    req
      .post('/orders', this.state.form)
      .then(() => {
        this.setState({ submitting: false, submitted: true })
      })
      .catch(err => {
        this.setState({
          submitting: false,
          error:
            'An error has occurred. Please try again./ Đã xảy ra lỗi. Xin vui lòng kiểm tra dữ liệu và thử lại.'
        })
      })
  }

  doShowNext = e => {
    e.preventDefault()
    if (!this.hasNext()) return
    this.setState({ currentViewIndex: this.state.currentViewIndex + 1 })
  }

  doShowPrev = e => {
    e.preventDefault()
    if (!this.hasPrev()) return
    this.setState({ currentViewIndex: this.state.currentViewIndex - 1 })
  }

  onFieldChange = e => {
    const { name, value } = e.target
    this.setState(({ form }) => {
      return { form: { ...form, [name]: value } }
    })
  }

  isChecked(name, value, form) {
    return form[name] === value
  }

  renderForm() {
    const Component = this.getCurrentView()
    return (
      <React.Fragment>
        <PageHeader>Order Form</PageHeader>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Unde
          praesentium labore consequatur, non harum neque quasi obcaecati
          temporibus, nostrum quo error odio et, voluptatibus rem nulla placeat
          accusantium quos. Inventore?
        </p>
        <Animate
          start={{ opacity: 0 }}
          enter={{
            opacity: [1],
            timing: { duration: 300, ease: easeQuadOut }
          }}>
          {styles => (
            <div style={styles}>
              <Component
                form={this.state.form}
                isChecked={this.isChecked}
                onFieldChange={this.onFieldChange}
              />
            </div>
          )}
        </Animate>

        <div className="flex items-center justify-between">
          {this.hasPrev() ? (
            <a href="#" className="f-serif f3 mt3 db" onClick={this.doShowPrev}>
              &larr; Back
            </a>
          ) : null}

          {this.hasNext() ? (
            <a
              href="#"
              className="f-serif f3 mt3 db ml-auto"
              onClick={this.doShowNext}>
              Next &rarr;
            </a>
          ) : this.isLast() ? (
            <button
              disabled={this.state.submitting}
              className="f-serif f3 mt3 db ml-auto bg-transparent bn navy pointer"
              onClick={this.doSubmit}>
              {this.state.submitting ? 'Submitting…' : 'Submit'}
            </button>
          ) : null}
        </div>
        {this.state.error != null ? (
          <p className="dark-red fw6">{this.state.error}</p>
        ) : null}
      </React.Fragment>
    )
  }

  renderCart() {
    return (
      <div className="db relative">
        <div className="absolute top-1 right-1">
          <button
            className="bn bg-transparent outline-transparent pointer"
            onClick={this.props.onRequestClose}>
            <img src={iconClose} alt="" />
          </button>
        </div>
        <div className="pv5-l grid grid-11-l grid-col-gap1">
          <div className="grid-col-span-3-l flex flex-column items-center justify-center h-100">
            <img className="db" src={left} style={{ maxWidth: '250px' }} />
          </div>
          <div className="grid-col-span-5-l lh-dbl fw3">
            {this.state.submitted ? <OrderThankYou /> : this.renderForm()}
          </div>
          <div className="grid-col-span-3-l flex flex-column items-center justify-center h-100">
            <img
              className="db tc center"
              src={right}
              style={{ maxWidth: '250px' }}
            />
          </div>
        </div>
      </div>
    )
  }

  render() {
    const { isOpen, onRequestClose } = this.props

    return (
      <ReactModal
        className="ReactModal__Content"
        overlayClassName="ReactModal__Overlay"
        onRequestClose={onRequestClose}
        isOpen={isOpen}>
        {this.renderCart()}
      </ReactModal>
    )
  }
}

export default withSiteData(Cart)



