import React from 'react'
import { withRouter } from 'react-static'
import FlowerModal from './FlowerModal'

class FlowerGrid extends React.Component {
  state = { modalVisible: false, currentProduct: null, currentUrl: null }

  doShowFlower = currentProduct => e => {
    e.preventDefault()

    this.setState(
      {
        currentProduct,
        modalVisible: true,
        currentUrl: this.props.location.pathname
      },
      () => {
        // Update URL but don't redraw
        window.history.pushState(
          this.props.location.state,
          currentProduct.name,
          currentProduct.url
        )
      }
    )
  }

  doCloseFlower = () =>
    this.setState(prevState => {
      // Restore URL
      window.history.pushState(
        this.props.location.state,
        '',
        prevState.currentUrl
      )
      return { modalVisible: false, currentProduct: null, currentUrl: null }
    })

  getSpan(spans, index) {
    return spans[index % spans.length]
  }

  render() {
    const spans = this.props.spans || [5, 3, 3, 3, 3, 5]

    return (
      <React.Fragment>
        <FlowerModal
          product={this.state.currentProduct}
          isOpen={this.state.modalVisible}
          onRequestClose={this.doCloseFlower}
        />
        <div className="pa2 pa0-l grid grid-11-l grid-rows-4 grid-col-gap1 mv3">
          {this.props.items.map((item, index) => {
            const props = {
              key: item.id || index,
              className: `cover bg-center mb3 grid-col-span-${this.getSpan(
                spans,
                index
              )}-l ${item.url != null ? 'pointer' : ''}`,
              style: { backgroundImage: `url(${item.image})` },
              ...(item.url != null ? { onClick: this.doShowFlower(item) } : {})
            }

            return <div {...props} />
          })}
        </div>
      </React.Fragment>
    )
  }
}

export default withRouter(FlowerGrid)



