import React from 'react'
import { Helmet } from 'react-helmet'
import { withSiteData, Link } from 'react-static'

import PageHeader from '../components/PageHeader'
import Footer from '../components/Footer'

export default withSiteData(
  class extends React.Component {
    render() {
      const styles = Array.from({ length: 8 }, (_, i) => i + 1).map(i => ({
        backgroundImage: `url(https://res.cloudinary.com/duqeezi8j/image/upload/v1529417963/ej/flower-${i}.jpg)`
      }))

      return (
        <div className="ph3-ns">
          <Helmet>
            <title>Studio :: El Julio</title>
          </Helmet>
          <div className="mv4-l pa2 pa0-l grid grid-1 grid-11-l grid-col-gap1 grid-row-gap1 grid-rows-4">
            <div
              className="grid-col-span-3-l cover bg-center"
              style={styles[0]}
            />
            <div className="grid-col-span-3-l self-center">
              <Link to="/studio/flower-design">
                <PageHeader withoutTilde={true}>Flower Design</PageHeader>
              </Link>
            </div>
            <div
              className="grid-col-span-5-l cover bg-center"
              style={styles[1]}
            />

            <div
              className="grid-col-span-5-l cover bg-center"
              style={styles[2]}
            />
            <div className="grid-col-span-3-l self-center">
              <Link to="/studio/diy">
                <PageHeader withoutTilde={true}>DIY</PageHeader>
              </Link>
            </div>
            <div
              className="grid-col-span-3-l cover bg-center"
              style={styles[3]}
            />

            <div className="grid-col-span-3-l self-center">
              <Link to="/studio/introduction">
                <PageHeader withoutTilde={true}>Introduction</PageHeader>
              </Link>
            </div>
            <div
              className="grid-col-span-3-l cover bg-center"
              style={styles[4]}
            />
            <div
              className="grid-col-span-5-l cover bg-center"
              style={styles[5]}
            />
          </div>
          <Footer />
        </div>
      )
    }
  }
)



