import React from 'react'
import DateTime from 'react-datetime'
import moment from 'moment'

import Radio from './Radio'

const PRODUCT_TYPES = [
  'Arrangement',
  'Bouquet (Stick Type)',
  'Bouquet (Round Type)',
  'Original Flower Box',
  'Stand Flower',
  'Box Gift',
  'Foliage Plant',
  'Orchid Bowl'
]

const PURPOSES = [
  'Birthday',
  'Dressing Room Flowers',
  'Condolence',
  'Farewell/ Retirement',
  'Inquiry',
  'Lobby Flowers',
  "Father's Day",
  "Mother's Day",
  'Opening/ Celebration',
  'Promoation Inaugural',
  'Relocation Holiday',
  'Wedding',
  'Wedding Anniversary',
  'Other Congratulations'
]

const ATMOSPHERES = [
  'Recommendation',
  'Simple/ Modern',
  'Cute',
  'I Feel Energic',
  'Bright and Gorgeous',
  'Natural',
  'Individual and Cool'
]

export default function(props) {
  const { form, onFieldChange, isChecked } = props
  return (
    <React.Fragment>
      <h3 className="f-serif f4 fw4">
        Input desired information/ Yêu cầu của bạn
      </h3>

      <p className="lh-copy">
        Please inspire us/ Hãy truyền cảm hứng cho chúng tôi
      </p>

      <div className="flex flex-column flex-row-l flex-wrap">
        <div className="w-100 mb3">
          <div className="w-50">
            <div className="bb b--navy">
              <label htmlFor="" className="db mv3 fw6">
                Choose your product/ Chọn loại sản phẩm
              </label>
              <select
                className="bn w-100 pv2 bg-transparent f-sans-serif navy fw3 outline-transparent f6"
                name="product_type"
                value={form.product_type}
                onChange={onFieldChange}>
                {PRODUCT_TYPES.map(type => (
                  <option key={type} value={type}>
                    {type}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        <div className="w-50 mb3 pr3">
          <div className="bb b--navy">
            <label htmlFor="" className="db mt3 fw6">
              Your budget/ Ngân sách
            </label>
            <input
              type="number"
              step="10000"
              min="0"
              className="bn pv2 w-100 outline-transparent f-sans-serif f5 navy fw3"
              style={{ marginTop: '.2rem' }}
              name="budget"
              value={form.budget}
              onChange={onFieldChange}
            />
          </div>
        </div>

        <div className="w-50 mb3 pr3">
          <div className="bb b--navy">
            <label htmlFor="" className="db mt3 fw6">
              Purpose/ Dùng để
            </label>
            <select
              className="bn w-100 pv2 bg-transparent f-sans-serif f6 navy fw3 outline-transparent"
              name="purpose"
              value={form.purpose}
              onChange={onFieldChange}>
              {PURPOSES.map(type => (
                <option key={type} value={type}>
                  {type}
                </option>
              ))}
            </select>
          </div>
        </div>

        <div className="w-100 mb3">
          <div className="w-50">
            <div className="bb b--navy">
              <label htmlFor="" className="db mv3 fw6">
                Atmosphere/ Cảm xúc của sản phẩm
              </label>
              <select
                className="bn w-100 pv2 bg-transparent f-sans-serif navy fw3 outline-transparent"
                name="atmosphere"
                value={form.atmosphere}
                onChange={onFieldChange}>
                {ATMOSPHERES.map(type => (
                  <option key={type} value={type}>
                    {type}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        <div className="w-40 pr3 mb3">
          <label htmlFor="" className="db fw6">
            Delivered at / Thời điểm giao hàng
          </label>
        </div>

        <div className="w-60 ph3 mb3">
          <DateTime
            dateFormat="DD/MM/YYYY"
            timeFormat="HH:mm"
            isValidDate={d => d.isAfter(moment().subtract(1, 'day'))}
            onChange={value =>
              onFieldChange({
                target: {
                  name: 'delivery_date',
                  value: new Date(value.toISOString())
                }
              })
            }
            value={form.delivery_date}
            renderInput={props => {
              return (
                <div className="bb b--navy">
                  <input
                    {...props}
                    className="bn pv2 w-100 outline-transparent f-sans-serif f6 navy fw3"
                  />
                </div>
              )
            }}
          />
        </div>

        <div className="w-40 pr3 mb3">
          <label htmlFor="" className="db fw6">
            Greeting Card/ Thiệp chúc mừng
          </label>
        </div>
        <div className="w-60 pl3 mb3 flex justify-between">
          <Radio
            name="greeting_card"
            value="yes_ej"
            onChange={onFieldChange}
            checked={isChecked('greeting_card', 'yes_ej', form)}>
            ELJULIO will provide
          </Radio>
          <Radio
            name="greeting_card"
            value="yes_own"
            onChange={onFieldChange}
            checked={isChecked('greeting_card', 'yes_own', form)}>
            Bring your own
          </Radio>
          <Radio
            name="greeting_card"
            value="no"
            onChange={onFieldChange}
            checked={isChecked('greeting_card', 'no', form)}>
            No
          </Radio>
        </div>

        {form.greeting_card === 'no' ? null : (
          <React.Fragment>
            <div className="w-100 mb3 pr3">
              <div className="bb b--navy">
                <label htmlFor="" className="db mt3 fw6">
                  Message/ Nội dung thiệp
                </label>
                <textarea
                  name="card_content"
                  value={form.card_content}
                  onChange={onFieldChange}
                  rows={5}
                  maxLength={250}
                  placeholder="Maximum 250 characters"
                  className="lh-copy bn pv2 w-100 outline-transparent f-sans-serif f6 navy fw3"
                />
              </div>
            </div>
            <div className="w-40 pr3 mb3">
              <label htmlFor="" className="db fw6">
                Card format/ Hình thức
              </label>
            </div>
            <div className="w-60 pl3 mb3 flex justify-between">
              <Radio
                name="card_type"
                value="handwriting"
                onChange={onFieldChange}
                checked={isChecked('card_type', 'handwriting', form)}>
                Hand-writing/ Viết tay
              </Radio>
              <Radio
                name="card_type"
                value="printing"
                onChange={onFieldChange}
                checked={isChecked('card_type', 'printing', form)}>
                Printing/ In sẵn
              </Radio>
            </div>
          </React.Fragment>
        )}

        <div className="w-100 mb3 pr3">
          <div className="bb b--navy">
            <label htmlFor="" className="db mt3 fw6">
              Remarks/ Ghi chú cho ELJULIO
            </label>
            <textarea
              name="remarks"
              value={form.remarks}
              onChange={onFieldChange}
              rows={5}
              placeholder="Anything we need to notice when handling this order? / Có điều gì ELJULIO cần lưu ý khi phục vụ bạn?"
              className="lh-copy bn pv2 w-100 outline-transparent f-sans-serif f6 navy fw3"
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}



