import React from 'react'
import PropTypes from 'prop-types'
import { Route, cleanPath } from 'react-static'
import { withContext, getContext } from 'recompose'
import { NodeGroup } from 'react-move'
import { easeQuadOut } from 'd3-ease'

export default getContext({
  router: PropTypes.object,
  staticURL: PropTypes.string
})(function({ getComponentForPath, router, staticURL }) {
  const render = props => {
    let Comp = getComponentForPath(cleanPath(props.location.pathname))
    if (!Comp) {
      Comp = getComponentForPath('404')
    }

    if (staticURL) {
      return (
        <div className="relative">
          <Comp {...props} />
        </div>
      )
    }

    return (
      <NodeGroup
        // React-move will handle the entry and exit of any items we pass in `data`
        data={[
          {
            // pass the current Comp, props, ID and router
            id: props.location.pathname,
            Comp,
            props,
            router
          }
        ]}
        keyAccessor={d => d.id}
        start={() => ({
          opacity: [0],
          scale: 1
        })}
        enter={() => ({
          opacity: [1],
          timing: { duration: 300, delay: 300, ease: easeQuadOut }
        })}
        update={() => ({
          opacity: [1]
        })}
        leave={() => ({
          opacity: [0],
          timing: { duration: 300, ease: easeQuadOut }
        })}>
        {nodes => (
          <div style={{ position: 'relative' }}>
            {nodes.map(({ key, data, state: { opacity, translateY } }) => {
              // Here, we override the router context with the one that was
              // passed with each route
              const PreservedRouterContext = withContext(
                {
                  router: PropTypes.object
                },
                () => ({
                  router: data.router
                })
              )(props => <div {...props} />)

              return (
                <PreservedRouterContext
                  key={key}
                  style={{
                    position: 'absolute',
                    top: 0,
                    right: 0,
                    bottom: 0,
                    left: 0,
                    transform: `translateY(${translateY}px)`,
                    opacity
                  }}>
                  <data.Comp {...data.props} />
                </PreservedRouterContext>
              )
            })}
          </div>
        )}
      </NodeGroup>
    )
  }

  return <Route path="*" render={render} />
})



