import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData, Link } from 'react-static'

import { MENU_STATION } from '../constants'

import PageHeader from '../components/PageHeader'
import Layout from '../components/Layout'
import FlowerGrid from '../components/FlowerGrid'

class Station extends React.Component {
  getHeader() {
    return (
      <div className="grid-col-span-9-l mb3">
        <PageHeader>{this.props.pageHeader || 'Station'}</PageHeader>

        <div className="mw8-ns center mt4-l">
          <p className="lh-copy dark-gray mt0 mb2 ph2 ph4-l f5 fw3">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Cum
            veritatis, excepturi quos. Itaque, unde. Sint a quod, est, sequi
            doloribus quisquam harum quaerat, nulla odit nisi voluptatum itaque
            quidem, dicta.
          </p>
        </div>
      </div>
    )
  }

  getContent() {
    return <FlowerGrid items={this.props.products || []} />
  }

  render() {
    return (
      <div className="ph3-ns">
        <Helmet>
          <title>{this.props.pageHeader} :: El Julio</title>
        </Helmet>
        <Layout header={this.getHeader()} submenu={MENU_STATION}>
          {this.getContent()}
        </Layout>
      </div>
    )
  }
}

export default withRouteData(Station)



