import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData, Link, RouteData } from 'react-static'

import flowers from './flowers'
import { MENU_STATION } from '../constants'

import PageHeader from '../components/PageHeader'
import Layout from '../components/Layout'

function Header() {
  return (
    <div className="grid-col-span-9-l mb3">
      <PageHeader>Recommend</PageHeader>
      <RouteData
        render={({ season }) =>
          season ? <h3 className="tc f3 fw4 f-serif">{season}</h3> : null
        }
      />
    </div>
  )
}

function Content() {
  return (
    <div className="pa2 pa0-l grid grid-11-l grid-rows-4 grid-col-gap1 mv3">
      <div
        className="cover bg-center mb3 grid-col-span-5-l"
        style={{ backgroundImage: `url(${flowers[2]})` }}
      />
      <div className="cover bg-center mb3 grid-col-span-3-l ph2 ph4-l">
        <p className="lh-copy dark-gray mt0 mb2 f5 fw3">
          Seasonal/ monthly ideas of flower gifts. Finely selected flowers and
          materials with a sense of the season.
        </p>
        <p className="mt4 f-serif f3">
          <Link to="/station/recommend/spring">Spring</Link>
          <span className="mh1">/</span>
          <Link to="/station/recommend/summer">Summer</Link>
        </p>
        <p className="mt4 f-serif f3">
          <Link to="/station/recommend/autumn">Autumn</Link>
          <span className="mh1">/</span>
          <Link to="/station/recommend/winter">Winter</Link>
        </p>
      </div>
      <div
        className="cover bg-center mb3 grid-col-span-3-l"
        style={{ backgroundImage: `url(${flowers[4]})` }}
      />
      <div
        className="cover bg-center mb3 grid-col-span-3-l"
        style={{ backgroundImage: `url(${flowers[5]})` }}
      />
      <div
        className="cover bg-center mb3 grid-col-span-3-l"
        style={{ backgroundImage: `url(${flowers[6]})` }}
      />
      <div
        className="cover bg-center mb3 grid-col-span-5-l"
        style={{ backgroundImage: `url(${flowers[7]})` }}
      />
    </div>
  )
}

export default withRouteData(function(props) {
  return (
    <div className="ph3-ns">
      <Helmet>
        <title>Station :: El Julio</title>
      </Helmet>
      <Layout header={<Header />} submenu={MENU_STATION}>
        <Content />
      </Layout>
    </div>
  )
})



