import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData } from 'react-static'

import flowers from './flowers'
import { MENU_STUDIO } from '../constants'

import PageHeader from '../components/PageHeader'
import Layout from '../components/Layout'
import FlowerGrid from '../components/FlowerGrid'

function Header(props) {
  return (
    <div className="grid-col-span-9-l mb3">
      <PageHeader>{props.pageHeader}</PageHeader>

      <div className="mw8-ns center mt4-l">
        <p className="lh-copy dark-gray mt0 mb2 ph2 ph4-l f5 fw3">
          ELJULIO STUDIO là một không gian sáng tạo hoa nghệ thuật theo yêu cầu
          của từng cá nhân hoặc từng doanh nghiệp. Chúng tôi làm việc như một
          agency sáng tạo, nhận "brief", cùng "brainstorm" và sáng tạo ra những
          ý tưởng khả thi nhằm mang đến một trải nghiệm thưởng thức hoa nghệ
          thuật độc đáo và duy nhất cho người thương hoặc cho khách hàng của
          bạn. Cùng chúng tôi tạo ra những khoảnh khắc đáng nhớ vì trong mỗi
          chúng ta đều là những "bông hoa" ngát hương và sống động.
        </p>
      </div>
    </div>
  )
}

function Content() {
  const items = flowers.map((image, id) => ({ id, image }))

  return <FlowerGrid items={items} />
}

export default withRouteData(function(props) {
  return (
    <div className="ph3-ns">
      <Helmet>
        <title>{props.pageHeader} :: El Julio</title>
      </Helmet>
      <Layout header={<Header {...props} />} submenu={MENU_STUDIO}>
        <Content />
      </Layout>
    </div>
  )
})



