import React from 'react'
import { Helmet } from 'react-helmet'
import { withSiteData, Link } from 'react-static'

import homeLeft from '../assets/img/home-left.jpg'
import homeRight from '../assets/img/home-left.jpg'
import station from '../assets/img/station.png'
import studio from '../assets/img/studio.png'
import logo from '../assets/img/logo.png'

export default withSiteData(function(props) {
  return (
    <div className="bg-near-white relative h-100vh">
      <Helmet>
        <title>Trang Chủ :: ELJULIO</title>
      </Helmet>
      <div className="absolute w-100 h-100vh flex flex-column flex-row-ns z-1">
        <div
          className="h-50 w-50-ns h-100-ns cover flex items-center justify-center"
          style={{ backgroundImage: `url(${homeLeft})` }}>
          <img src={station} alt="" className="mw-40vw mw-10vw-l" />
        </div>
        <div
          className="h-50 w-50-ns h-100-ns cover flex items-center justify-center"
          style={{ backgroundImage: `url(${homeRight})` }}>
          <img src={studio} alt="" className="mw-40vw mw-10vw-l" />
        </div>
      </div>

      <div className="clip-s absolute w-100 h-100vh flex items-center justify-center z-2">
        <img src={logo} alt="ELJULIO" className="mw-25vw" />
      </div>

      <div className="absolute w-100 h-100vh flex flex-column flex-row-ns z-3">
        <Link to="/station" className="h-50 w-50-ns h-100-ns ej-home-cell" />
        <Link to="/studio" className="h-50 w-50-ns h-100-ns ej-home-cell" />
      </div>
    </div>
  )
})



