import React from 'react'
import Radio from './Radio'

export default function(props) {
  const { form, onFieldChange, isChecked } = props
  return (
    <div>
      <h3 className="f-serif f4 fw4">Payment/ Hình thức thanh toán</h3>
      <p className="lh-dbl fw3">Please select your preferred method</p>
      <Radio
        name="payment_type"
        value="bank_transfer"
        onChange={onFieldChange}
        checked={isChecked('payment_type', 'bank_transfer', form)}>
        Bank Transfer/ Chuyển khoản ngân hàng
      </Radio>
      <Radio
        name="payment_type"
        value="credit_card"
        onChange={onFieldChange}
        checked={isChecked('payment_type', 'credit_card', form)}>
        Credit Card/ Thẻ tín dụng
      </Radio>
      <Radio
        name="payment_type"
        value="cod"
        onChange={onFieldChange}
        checked={isChecked('payment_type', 'cod', form)}>
        Cash on Delivery/ Thanh toán khi giao hàng
      </Radio>
    </div>
  )
}



