import React from 'react'
import { Helmet } from 'react-helmet'
import { withSiteData } from 'react-static'

import PageHeader from '../components/PageHeader'
import Layout from '../components/Layout'

function Header() {
  return (
    <div className="grid-col-span-9-l">
      <PageHeader>Đường dẫn không tồn tại</PageHeader>
      <p className="tc lh-copy mh5-l">Trang web bạn muốn xem không tồn tại. Vui lòng kiểm tra lại URL.</p>
    </div>
  )
}

export default withSiteData(function(props) {
  return (
    <div className="ph3-ns">
      <Helmet>
        <title>404 :: El Julio</title>
      </Helmet>
      <Layout header={<Header />} />
    </div>
  )
})



