export const NUANCES = {
  ALL: '',
  BRIGHT: 'Bright and Gorgeous',
  FRESH: 'Fresh and Natural',
  SIMPLE: 'Simple and Modern',
  CUTE: 'Cute',
  OTHER: 'Other'
}

export function makeLabels(obj, valueOfAll) {
  return Object.entries(obj).map(([, value]) => ({
    value,
    label: value === valueOfAll ? 'All' : value
  }))
}



