import React from 'react'
import Radio from './Radio'

export default class extends React.Component {
  render() {
    const { categories, nuances, isFilterEnabled, doSetFilter } = this.props
    return (
      <div className="flex flex-column flex-row-l flex-wrap dark-gray mt5-l mb4-l ph4 f6">
        <div className="w-10-l fw6 fw4-l pv3 pv0-l tr-l pr3">Categories</div>
        <div className="w-40-l">
          <ul className="list flex flex-wrap ma0 tl bl b--dark-gray pl3">
            {categories.map(cat => (
              <li className="w-50 pb3 flex" key={cat.label}>
                <Radio
                  checked={isFilterEnabled('category', cat.value)}
                  onChange={doSetFilter('category', cat.value)}
                  value={cat.value}
                  name="categories">
                  {cat.label}
                </Radio>
              </li>
            ))}
          </ul>
        </div>
        <div className="w-10-l fw6 fw4-l pv3 pv0-l tr-l pr3">Nuance</div>
        <div className="w-40-l">
          <ul className="list flex flex-wrap ma0 tl bl b--dark-gray pl3">
            {nuances.map(el => (
              <li className="w-50 mb3 flex" key={el.label}>
                <Radio
                  checked={isFilterEnabled('nuance', el.value)}
                  onChange={doSetFilter('nuance', el.value)}
                  value={el.value}
                  name="nuance">
                  {el.label}
                </Radio>
              </li>
            ))}
          </ul>
        </div>
      </div>
    )
  }
}



