import React from 'react'
import { Link } from 'react-static'
import Animate from 'react-move/Animate'
import { easeQuadOut } from 'd3-ease'

import logoFull from '../assets/img/logo-full.png'
import slogan from '../assets/img/slogan.png'
import iconCart from '../assets/img/icon-cart.png'
import iconMenu from '../assets/img/icon-menu.png'
import iconClose from '../assets/img/icon-close.png'
import iconFacebook from '../assets/img/icon-facebook.png'
import iconInstagram from '../assets/img/icon-instagram.png'
import iconEmail from '../assets/img/icon-email.png'

import Footer from './Footer'
import Cart from './Cart'

export default class Layout extends React.Component {
  state = {
    menuOpen: false,
    cartOpen: false
  }

  doToggleMenu = e => {
    e && e.preventDefault()
    this.setState({ menuOpen: !this.state.menuOpen })
  }

  doToggleCart = e => {
    e.preventDefault()
    this.setState({ cartOpen: !this.state.cartOpen })
  }

  renderSubmenu(config) {
    return (
      <Animate
        show={!this.state.menuOpen}
        start={{ opacity: 0, translateX: -100 }}
        enter={{
          opacity: [1],
          translateX: [0],
          timing: { duration: 300, ease: easeQuadOut }
        }}>
        {({ opacity, translateX }) => (
          <div
            className="flex items-center justify-between bb b--navy absolute absolute--fill"
            style={{ opacity, transform: `translateX(${translateX}%)` }}>
            <Link to={config.to} className="lh-copy f5 fw6">
              {config.name}
            </Link>
            <ul className="list ma0 tr f5">
              {config.menus.map(menu => (
                <li className="mb2" key={menu.label}>
                  <Link className="fw3" {...menu.link}>
                    {menu.label}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        )}
      </Animate>
    )
  }

  renderMenu() {
    return (
      <div
        className="relative flex items-center"
        style={
          this.props.submenu || this.state.menuOpen ? { height: '10rem' } : {}
        }>
        <Animate
          show={this.state.menuOpen}
          start={{ opacity: 0, translateX: 100 }}
          enter={{
            opacity: [1],
            translateX: [0],
            timing: { duration: 300, ease: easeQuadOut }
          }}
          leave={{
            translateX: [100],
            opacity: [0],
            timing: { duration: 300, ease: easeQuadOut }
          }}>
          {({ opacity, translateX }) => (
            <ul
              className="list ma0 pa0 absolute absolute--fill"
              style={{ opacity, transform: `translateX(${translateX}%)` }}>
              <li className="tr pv3 db bb b--navy">
                <Link to="/about">About Us</Link>
              </li>
              <li className="tr pv3 db bb b--navy">
                <Link to="/how-to-order">How to Order</Link>
              </li>
              <li className="tr pv3 db bb b--navy">
                <Link to="/order-now">Order Now</Link>
              </li>
            </ul>
          )}
        </Animate>
        {this.props.submenu != null
          ? this.renderSubmenu(this.props.submenu)
          : null}
      </div>
    )
  }

  render() {
    return (
      <React.Fragment>
        <main>
          <div className="pa2 pa0-l grid grid-1 grid-11-l grid-col-gap1">
            <div className="grid-col-span-2-l">
              <div className="flex items-center justify-between bb b--navy pv3-l">
                <Link to="/">
                  <img src={logoFull} alt="" style={{ width: '9rem' }} />
                </Link>
                <div className="mt2">
                  <a className="ml3" href="#" onClick={this.doToggleCart}>
                    <img src={iconCart} alt="" style={{ width: '24px' }} />
                  </a>
                  <a
                    className="ml3 outline-transparent"
                    href="#"
                    onClick={this.doToggleMenu}>
                    <img
                      src={this.state.menuOpen ? iconClose : iconMenu}
                      alt=""
                      style={{ width: '24px' }}
                    />
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div className="pa2 pa0-l grid grid-1 grid-11-l grid-rows-3 grid-col-gap1 grid-row-gap1">
            <div className="grid-col-span-2-l">
              {this.renderMenu()}

              <div className="bb b--navy flex items-center justify-between pv1">
                <img src={slogan} alt="" className="w-50" />
                <ul className="list ma0 pa0 flex items-center">
                  <li className="mh1">
                    <a href="#">
                      <img
                        src={iconFacebook}
                        alt=""
                        style={{ width: '20px' }}
                      />
                    </a>
                  </li>
                  <li className="mh1">
                    <a href="#">
                      <img
                        src={iconInstagram}
                        alt=""
                        style={{ width: '20px' }}
                      />
                    </a>
                  </li>
                  <li className="mh1">
                    <a href="#">
                      <img src={iconEmail} alt="" style={{ width: '20px' }} />
                    </a>
                  </li>
                </ul>
              </div>
            </div>

            {this.props.header}
          </div>

          {this.props.children}
        </main>
        <Footer doOpenOrderForm={this.doToggleCart} />
        <Cart isOpen={this.state.cartOpen} onRequestClose={this.doToggleCart} />
      </React.Fragment>
    )
  }
}



